
* {
    margin: 0;
    padding: 0;
  
}


.header > nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5.5vw;
    background-color: #1d5276; 
    
}



.header > nav > span > img {
 
    width: auto;
}


.header > nav > .ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1vw;
}


.header > nav > .ul > .nav {
    cursor: pointer;
    margin: 3px;
    padding: 8px;
    color: #fff; 
    font-weight: 500;
    text-decoration: none;
    
}


.header > nav > .ul > .nav:hover {
    color: #1d5a83; 
    background-color: #fff;
   

}


.header > nav > span > img {
height: 50px;
}
